
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Search from 'vux/src/components/search/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components: {
Search,
Scroller,
XHeader,
Divider,
TabItem,
Tab
},
data () {
return {
host: config.DOMAIN_HOST,
note: '',
searchValue: '',
shoptags: [],
currentItem: 0,
msgContentHeight: 0,
isActive: 1,
isActiveTab: 1,
page: 1,
list: [],
}
},
mounted () {
let footer = document.getElementById('footer').offsetHeight

window.msgContentHeight = document.documentElement.clientHeight
this.msgContentHeight = (window.msgContentHeight - footer - 60) + 'px'
window.onresize = () => {
return (() => {
window.msgContentHeight = document.documentElement.clientHeight
this.msgContentHeight = (window.msgContentHeight - footer - 60) + 'px'
})()
}

this.getShance();
},
created () {


},
methods: {
submit(){
let that = this;
that.list = []
layui.use('flow', function(){
let flow = layui.flow;
let $ = layui.jquery
,element = layui.element;
flow.load({
elem: '#all' 
,isAuto:true
,scrollElem: '#all' //滚动条所在元素，一般不用填，此处只是演示需要。
,loadMore:'#loadMore'
,done: function(page, next){
that.$api.shop({'search': that.searchValue,}).then(res => {
that.loading = false
that.list = that.list.concat(res.data.results)
next('', page < Math.ceil(res.data.count / 10 ));
})
}
});
});
},
getShance(){
let that = this;
layui.use('flow', function(){
let flow = layui.flow;
let $ = layui.jquery
,element = layui.element;

flow.load({
elem: '#all' 
,isAuto:true
,scrollElem: '#all' //滚动条所在元素，一般不用填，此处只是演示需要。
,loadMore:'#loadMore'
,done: function(page, next){
let lis = [];
that.$api.shop({'type': this.note, 'page': page}).then(res => {
that.list = that.list.concat(res.data.results)
console.log(res.data.results);
next('', page < Math.ceil(res.data.count / 10 ));
})

}
});
});
},
}
}



